import React, { Component } from 'react'

class List extends Component {
  constructor(){
    super();
    this.state = {
        inpValue:'',
    }
}
render() {
       
  const {todos} = this.props.data;
  let arr1 = todos.filter(item =>!item.done);
  return (
      <div>
           { <h2>正在运行{arr1.length}</h2> }
          <ul>
              {
                  this.props.renderTodos(false)
              }
          </ul>
          <h2>运行结束</h2>
          <ul>
              {
                  this.props.renderTodos(true)
              }
          </ul>
      </div>
  )
}
}
export default List
