import React, { Component } from 'react'
import Input from './Input'
import List from './List'
export  class Todolist extends Component {
    constructor(){
        super();
        this.state = {
            inpValue:'',
            todos:[]
        }
    }
    componentDidMount(){
        let todos = localStorage.getItem('todos');
        if(todos){
            this.setState({
                todos:JSON.parse(todos)
            })
        }
    }
    componentDidUpdate(){
        localStorage.setItem('todos',JSON.stringify(this.state.todos));
     }
    handleChange = (e)=>{
        this.setState({inpValue:e.target.value})
    }
    addTodo =()=>{
        if(this.state.inpValue===''){
            alert('null');
            return 0 ;
        }
        this.setState({todos:[{title:this.state.inpValue,done:false},...this.state.todos]});
        this.setState({inpValue:''});
    }
    deleteTodo=(idx)=>{
        this.setState({
            todos:this.state.todos.filter((item,index)=>{
                if(index!==idx){
                    return true;
                }else{
                    return false;
                }
            })
        });
    }
    toggle =(idx)=>{
        let newTodos = JSON.parse(JSON.stringify(this.state.todos));
        newTodos[idx].done = !newTodos[idx].done;
        this.setState({
            todos:newTodos
        })
    }
    renderTodos =(done)=>{
            return this.state.todos.map((todo,idx)=>{
                if(done===todo.done){
                    return <li>
                        <input 
                        onClick = {()=>this.toggle(idx)}
                        checked={done} 
                        type='checkbox'/>
                        <span dangerouslySetInnerHTML={{__html:todo.title}}></span>
                        <button onClick={()=>this.deleteTodo(idx)}>删除</button>
                    </li>
                }
             })
        }
    render() {
        return (
            <div>
                <Input data = {this.state} handleChange = {this.handleChange.bind(this)} addTodo = {this.addTodo.bind(this)} /> 
                <List data = {this.state} renderTodos = {this.renderTodos.bind(this)}/>
            </div>
        )
    }
}
export default Todolist 
